/* Contrast */
// The contrast ratio to reach against white, to determine if color changes from "light" to "dark". Acceptable values for WCAG 2.0 are 3, 4.5 and 7.
// See https://www.w3.org/TR/WCAG20/#visual-audio-contrast-contrast
$min-contrast-ratio:   4.5;

// Customize the light and dark text colors for use in our color contrast function.
$color-contrast-dark:      $gray-800;
$color-contrast-light:     $gray-200;

  $grays             : (
    100              : $gray-100,
    200              : $gray-200,
    300              : $gray-300,
    400              : $gray-400,
    500              : $gray-500,
    600              : $gray-600,
    700              : $gray-700,
    800              : $gray-800,
    900              : $gray-900
  );


  $colors            : (
  "blue"             : $blue,
  "indigo"           : $indigo,
  "purple"           : $purple,
  "pink"             : $pink,
  "red"              : $red,
  "yellow"           : $yellow,
  "orange"           : $orange,
  "green"            : $green,
  "teal"             : $teal,
  "cyan"             : $cyan,
  "silver"           : $silver,
  "chocolate"        : $chocolate,
  "camel"            : $camel,
  "saltpan"          : $saltpan,
  "white-chocolate"  : $white-chocolate,
  "dark-vanilla"     : $dark-vanilla,
  "ghost-white"      : $ghost-white,
  "deep-sky-blue"    : $deep-sky-blue,
  "coral"            : $coral,
  "maroon"           : $maroon,
  "light-cyan"       : $light-cyan, /* Light Green */
  "neon-blue"        : $neon-blue, /* light blue */
  "salomie"          : $salomie, /* light orange */
  "wild-watermelon"  : $wild-watermelon, /* light red */
  "hot-pink"         : $hot-pink
  );


  $theme-colors      : (
    "primary-color"  : #0abab5,
    "secondary-color": #ffe41e,
    "accent-color"   : #a8a9ad,
    "valid-color"    : #cdffff,
    "info-color"     : #555aff,
    "alert-color"    : #ffd080,
    "invalid-color"  : #ff555a,
    "attention-color": #ff6fa9
  );
