/* ____________________________________ Utilities Colors */
  /* Brand Color Variables */
    $primary-color       : #0abab5;
    $secondary-color     : #ffe41e;
    $accent-color        : #a8a9ad;

  /* Functional Color Variables */
    $light-cyan          : #cdffff; /* Light Green */
    $neon-blue           : #555aff; /* light blue */
    $salomie             : #ffd080; /* light orange */
    $wild-watermelon     : #ff555a; /* light red */
    $hot-pink            : #ff6fa9;


/* ____________________________________ THEME */
  /* Utility Colors */
    $valid-color         : $light-cyan;
    $info-color          : $neon-blue;
    $alert-color         : $salomie;
    $invalid-color       : $wild-watermelon;
    $attention-color     : $hot-pink;

  /* Mix Colors */
  $border-mix-color      : $gray-800;
  $muted-mix-color       : $gray-700;
  $shadow-mix-color      : $gray-600;
  $inset-mix-color       : $gray-300;

  /* Typography */
  $body-text-color       : $primary-color;
  $headings-text-color   : null;
  $footer-text-color     : $accent-color;

  $muted-text-color      : color-mix( in srgb, $accent-color $muted-percentage, $muted-mix-color);

/* ____________________________________ FOUNDATION */
  /* Border */
  $border-color          : color-mix( in srgb, $primary-color $border-percentage, $border-mix-color);
  $shadow-color          : color-mix( in srgb, $primary-color $shadow-percentage, $shadow-mix-color);

  /* Link */
  $active-color          : color-mix( in srgb, $primary-color 40%, $white);
  $disabled-color        : color-mix( in srgb, $primary-color 40%, $black);

  $link-text-color       : $active-color;
  $nav-link-color        : $active-color;
    // Navbar
    $navbar-dark-text    : $accent-color;
    $navbar-light-text   : $primary-color;

  /* Lists */
  $list-group-text-color : $gray-900;

  /* Background */
  @import "patterns";

  $body-bg-color         : $primary-color-gradient;
  $page-bg-color         : tint-color($primary-color, $background-percentage);
  $main-bg-color         : tint-color($accent-color, $background-percentage);


/* ______________________________________________________ BACKGROUNDS */
  /* Theme BG Colors */
  $primary-bg            : tint-color($primary-color, $background-percentage);
  $secondary-bg          : tint-color($secondary-color, $background-percentage);
  $accent-bg             : tint-color($accent-color, $background-percentage);
  $valid-bg              : tint-color($valid-color, $background-percentage);
  $alert-bg              : tint-color($alert-color, $background-percentage);
  $invalid-bg            : tint-color($invalid-color, $background-percentage);
  $attention-bg          : tint-color($attention-color, $background-percentage);
  $info-bg               : tint-color($info-color, $background-percentage);
