/* ____________________________________ BORDERS */
/* Colors */
$border                  : $border-color;

$border-width            : $base-width;
$border-widths           : (
  1                      : 1px,
  2                      : 2px,
  3                      : 3px,
  4                      : 4px,
  5                      : 5px
);

$border-width-sm         : 1;
$border-width-md         : 2;
$border-width-lg         : 3;
$border-width-focus      : divide($xs, 3);
$border-width-hover      : $border-width-lg;

$border-radius           : $base-radius;
$border-radius-sm        : $border-radius * $sm;
$border-radius-md        : $border-radius * $md;
$border-radius-lg        : $border-radius * $lg;
$border-radius-xl        : $border-radius * $xl;
$border-radius-xxl       : $border-radius * $xxl;
$border-radius-pill      : 50rem;

$btn-border-radius       : $border-radius-sm;
$btn-focus-width         : $border-width-focus;
$card-border-radius      : $border-radius-lg;
$card-border-radius-inner: $border-radius-sm;
