

/* BOX SHADOW */
$box-shadow: 1.75rem 1.5rem 1.75rem $shadow-color;
$box-shadow-sm: 1.25rem 1rem 1rem $primary-color 35%, -1rem -.75rem -.75rem $gray-800 15%;

$box-shadow-lg: 2.25rem 1.75rem 1.25rem $primary-color 35%, -2rem -1.5rem -.25rem $gray-800 15%;

$box-shadow-hover: .25rem .50rem .75rem $shadow-hover;
$box-shadow-focus: -.25rem .50rem .75rem $shadow-focus, -.25rem -.50rem .75rem tint-color(rgba(50,50,50), 25%);

$box-shadow-inset: inset -.25rem -.25rem -.50rem 0px rgba($primary-color, .7) 25%, inset .25rem .25rem .50rem 0px rgb($accent-color, .35) 25%;

$masthead-shadow: 0 1.2rem .9rem 1rem shift-color($shadow-color, $active-percentage);

/* CARD SHADOW */
$card-box-shadow: 1.5rem 1.25rem 1.25rem $shadow-color;
$card-box-shadow-inset: $shadow-inset-a -.25rem -.25rem -.50rem 0px inset, $shadow-inset-b .25rem .25rem .50rem 0px inset;

/* INPUT SHADOW */
$input-box-shadow: 0 0 $input-btn-focus-blur $border-width-focus $input-text-focus;

/* BUTTON SHADOW */
$button-shadow: inset 0 1px 0 rgba($white, .15), 0 1px 1px rgba($primary-color, .075);
$btn-shadow-focus: inset 0 -1px 0 rgba($white, .15), 0 -1px 1px rgba($primary-color, .075);


/* LOGO SHADOW */
.logo-shadow {
  filter: drop-shadow(20px 10px 1px rgba(0, 0, 0, 0.432));
}

  /* GROUP SHADOW */
  .hgroup {
    filter: drop-shadow(20px 10px 1px rgba(0, 0, 0, 0.432));
  }

  .otto {
    background: #0e8dbc;
    color: $secondary-color;
    text-shadow: 0 1px 0 #ccc,
                 0 2px 0 #c9c9c9,
                 0 3px 0 #bbb,
                 0 4px 0 #b9b9b9,
                 0 5px 0 #aaa,
                 0 6px 1px rgba(0,0,0,.1),
                 0 0 5px rgba(0,0,0,.1),
                 0 1px 3px rgba(0,0,0,.3),
                 0 3px 5px rgba(0,0,0,.2),
                 0 5px 10px rgba(0,0,0,.25),
                 0 10px 10px rgba(0,0,0,.2),
                 0 20px 20px rgba(0,0,0,.15);
               }


/* https://css-tricks.com/almanac/properties/b/box-shadow/ */

//Shadows to create 3D effects

//Shadow under right side to appear lifted
.right-shadow
{
  position: relative;
}
.right-shadow:before
{
   $right-shadow: 286deg 36% 51%;
  z-index: -1;
  position: absolute;
  content: "";
  bottom: 15px;
  left: 0;
  width: 75%;
  top: 80%;
  max-width:600px;
  background: $shadow-color;
  box-shadow: -0.9px 0.2px 1px hsl( 286deg, 36%, 51%,  0.49),
    -2.7px 0.8px 3px -1px hsl( 286deg, 36%, 51%, 0.45),
    -7.2px 2.1px 7.9px -2px hsl( 286deg, 36%, 51%, 0.41),
    -18.3px 5.3px 20.1px -2.9px hsl( 286deg, 36%, 51%, 0.38);
  transform: rotate(-16deg);
}

.left-and-right-shadow
{
  position: relative;
}
.left-and-right-shadow:before, .left-and-right-shadow:after
{
  z-index: -1;
  position: absolute;
  content: "";
  bottom: 25px;
  left: 10px;
  width: 50%;
  top: 80%;
  max-width:300px;
  background: $shadow-color;
  -webkit-box-shadow: 0 50px 20px $shadow-color;
  -moz-box-shadow: 0 50px 20px $shadow-color;
  box-shadow: 0 50px 20px $shadow-color;
  -webkit-transform: rotate(-8deg);
  -moz-transform: rotate(-8deg);
  -o-transform: rotate(-8deg);
  -ms-transform: rotate(-8deg);
  transform: rotate(-8deg);
}
.left-and-right-shadow:after
{
  -webkit-transform: rotate(8deg);
  -moz-transform: rotate(8deg);
  -o-transform: rotate(8deg);
  -ms-transform: rotate(8deg);
  transform: rotate(8deg);
  right: 10px;
  left: auto;
}
