// Options
//
// Quickly modify global styling by enabling or disabling optional features.

$enable-caret                    : true;
$enable-rounded                  : true;
$enable-shadows                  : true;
$enable-gradients                : true;
$enable-transitions              : true;
$enable-reduced-motion           : true;
$enable-smooth-scroll            : true;
$enable-grid-classes             : true;
$enable-container-classes        : true;
$enable-cssgrid                  : true;
$enable-button-pointers          : true;
$enable-rfs                      : true;
$enable-validation-icons         : true;
$enable-negative-margins         : true;
$enable-deprecation-messages     : true;
$enable-important-utilities      : true;

// Prefix for                    :root CSS variables

$variable-prefix                 : ca-;

/* ________________________ PROPERTIES CONTROLED BY DEFAULT*/

/* _____________________________________ SPACING */
  /* Base Number */
  $base-unit                   : 1.15;
  $base: $base-unit;
	/* Map */
	$offsets                : (
	  0                          : 0,
	  1                          : $base-unit * .25,
	  2                          : $base-unit * .5,
	  3                          : $base-unit,
	  4                          : $base-unit * 1.5,
	  5                          : $base-unit * 3
	  );

  $negative-offset               : negativify-map($base-unit);

  $vertical-base                 : 1vh;
  $horizontal-base               : 1vw;

  /* Base Size */
  $base-size                     : 1.05;
	$xs                          : $base-size * .75; // .86 14px 10.5pt
	$sm                          : $base-size * .875; // 1.0 16px 12pt medium
	$md                          : $base-size * 1.05; // 1.20 19px 14pt
	$lg                          : $base-size * 1.25; // 1.44 23px 17pt
	$xl                          : $base-size * 1.50; // 1.73 26-29px 20-22pt
	$xxl                         : $base-size * 1.75; // 2.01 32px 24pt

	/* Map */
	$sizes                       : (
	  'xs'                       : $xs,
	  'sm'                       : $sm,
	  'md'                       : $md,
	  'lg'                       : $lg,
	  'xl'                       : $xl,
	  'xxl'                      : $xxl
	);

  /* Base Offset */
  $base-offset                   : 16px;

  /* Block Spacing */
  $block-spacing                 : 1.5rem;

/* _____________________________________ POSITIONING */
	/* Position Values Map */
	$position-values             : (
	  0                          : 0,
	  25                         : 25%,
	  50                         : 50%,
	  75                         : 75%,
	  100                        : 100%
	);

	/* Aspect Ratios Map */
	$aspect-ratios               : (
	  '1x1'                      : 100%,
	  '4x3'                      : calc(3 / 4 * 100%),
	  '16x9'                     : calc(9 / 16 * 100%),
	  '21x9'                     : calc(9 / 21 * 100%)
	);

/* _____________________________________ ESCAPED CHARACTERS */
  // Characters which are escaped by the escape-svg function
  $escaped-characters: (
	("<", "%3c"),
	(">", "%3e"),
	("#", "%23"),
	("(", "%28"),
	(")", "%29"),
  );


/* _____________________________________ PADDING */
  @import "components/abstracts/variables/padding";

/* _____________________________________ GRID */
  $grid-columns                  : 16;
  $grid-row-columns              : 16;
  $grid-gutter                   : 16px;

/* ____________________________________ BORDERS */
  $base-width                    : 1px;
  $base-radius                   : .95rem;

/* ____________________________________ ANIMATION */
  $animation-speed               : 250ms;
  $animation                     : ease-in-out;

/* _____________________________________ LINK */
  $link-color              : $active-color;
  $link-decoration          : bold;

  $hover-color              : shift-color($active-color, $link-hover-percentage);
  $hover-decoration         : underline, bold;

  $focus-color              : tint-color($active-color, $focus-percentage);

  $visited-color            : shade-color($active-color, $link-visited-percentage);
  $visited-hover-color      : shade-color($active-color, $link-visited-percentage);
  $visited-decoration       : underline;


  /* Nav */
  $nav-color           : $secondary-color;
  $nav-hover           : color-mix( in srgb $nav-color $hover-percentage, $white);
  $nav-border          : darken($nav-color, $border-percentage);
  $nav-bg              : $gradient-tabs-bg;
  $tabs-color          : $nav-color;
  $tabs-hover          : shade-color($nav-hover, $hover-percentage);
  $tabs-border         : $tabs-color;
  $tabs-bg             : $gradient-tabs-bg;


  $navbar-dark-text              : $accent-color;
  $navbar-dark-hover             : shift-color($navbar-dark-text, $hover-percentage);
  $navbar-dark-disabled-color     : color-mix( in srgb $navbar-dark-text $disabled-percentage, $disabled-color);
  $navbar-dark-toggler-border    : $navbar-dark-text;
  $navbar-light-text             : $primary-color;
  $navbar-light-hover            : shift-color($navbar-light-text, $hover-percentage);
  $navbar-light-disabled-color    : color-mix( in srgb $navbar-light-text $disabled-percentage, $disabled-color);
  $navbar-light-toggler-border   : $navbar-dark-text;

/* ____________________________________________ Buttons + Form */

  $input-text-color                : darken($primary-color, 50%);
  $input-bg-color                  : tint-color($primary-color, 35%);
  $input-border-color              : shade-color($primary-color, $border-percentage);

  $input-disabled-bg-color         : color-mix($input-text-color $disabled-bg-percentage, $disabled-color);
  $input-disabled-border-color     : color-mix($input-text-color $disabled-border-percentage, $disabled-color);

  $input-text-focus          : shade-color($input-text-color, $focus-percentage);
  $input-focus-bg-color            : shade-color($input-bg-color, $focus-percentage);
  $input-focus-border-color        : shade-color($input-border-color, $focus-percentage);

  $input-placeholder-text-color    : tint-color($input-text-color, $muted-percentage);
  $input-plaintext-text-color      : tint-color($primary-color, $muted-percentage);

  $form-file-button-hover-bg-color : shade-color($input-bg-color, $hover-percentage);

  $input-group-bg-color            : tint-color($accent-color, $background-percentage);
  $form-switch-text-color          : $input-bg-color;

  /* __________________________________________________ Lists */

  $list-group-text-color           : $gray-900;
  $list-group-bg-color             : tint-color($list-group-text-color, $background-percentage);
  $list-group-border-color         : shade-color($list-group-text-color, $border-percentage);
  $list-group-hover-bg             : shift-color($list-group-bg-color, $hover-percentage);
  $list-group-bg-color             : shift-color($list-group-text-color, $hover-percentage);
  $list-group-disabled-bg-color    : color-mix($list-group-bg-color $disabled-bg-percentage, $disabled-color);
