/* _____________________________________ SHADOWS */
$shadow-button                   : color-mix( in srgb $shadow-color $shadow-percentage, $shadow-mix-color);
$shadow-hover                    : tint-color($shadow-color, $hover-percentage);
$shadow-focus                    : shift-color($shadow-color, $focus-percentage);
$shadow-inset-a                  : color-mix( in srgb $shadow-color $inset-perecentage, $shadow-mix-color);
$shadow-inset-b                  : tint-color($shadow-color, $inset-perecentage);
